import { SyntheticEvent, useState } from "react"
import Box from "@mui/material/Box"
import Tab from "@mui/material/Tab"
import Tabs from "@mui/material/Tabs"
import { RankingDetectionsPanel } from "./RankingDetectionsPanel"

const a11yProps = (index: number) => {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
    sx: { p: 1 },
  }
}

type Props = {
  sentence: { entities: Array<string>; topics: Array<string> }
  headline: { entities: Array<string>; topics: Array<string> }
}

const StoryCardRankingDetections = ({ sentence, headline }: Props) => {
  const hasHeadline = headline.entities.length + headline.topics.length !== 0
  const hasSentence = sentence.entities.length + sentence.topics.length !== 0
  const [value, setValue] = useState(0)
  const handleChange = (event: SyntheticEvent, newValue: number) => {
    setValue(newValue)
    event.stopPropagation()
  }

  return (
    <Box sx={{ width: "100%", background: "rgba(255, 255, 255, 0.16)", minWidth: "320px" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs value={value} onChange={handleChange} onClick={event => event.stopPropagation()}>
          {hasHeadline ? (
            <Tab
              onClick={event => event.stopPropagation()}
              label={`Headline match (${headline.entities.length + headline.topics.length})`}
              {...a11yProps(0)}
            />
          ) : null}
          {hasSentence ? (
            <Tab
              onClick={event => event.stopPropagation()}
              label={`Sentence match (${sentence.entities.length + sentence.topics.length})`}
              {...a11yProps(1)}
            />
          ) : null}
        </Tabs>
      </Box>
      {hasHeadline ? (
        <RankingDetectionsPanel value={value} index={0} detections={headline} />
      ) : null}
      <RankingDetectionsPanel value={value} index={hasHeadline ? 1 : 0} detections={sentence} />
    </Box>
  )
}

export { StoryCardRankingDetections }
